import React, { useEffect, useState } from 'react'
import {
  Grid, Table, TableContainer, IconButton,
  TableHead, TableBody, TableRow, TableCell, Paper, Collapse
} from '@material-ui/core'
import {
  DateField,
  List,
  ListProps,
  TextField,
  useListContext
} from 'react-admin'
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown'
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import { useHistory } from 'react-router-dom'
import { useSelector } from 'react-redux';
import { ExtendedLog } from '../../core/logs/extended-log'
import { ResourceName } from '../../core/ResourceName';
import { getContrastColor } from '../../core/common/visual.utils';
import { reducerType } from '../../core/common/theme.reducer';
import { BookingButton, CenterGrid, NoPaddingCell } from './LogsComponents';

const DashRow = ({ data }: { data: ExtendedLog }) => {
  const [open, setOpen] = useState(false)
  const [color, setColor] = useState("")
  const history = useHistory()
  const textColor = useSelector((state: reducerType) => state.theme.darkMode === 'light' ? 'black' : 'white')

  useEffect(() => {
    if (data.color === null) {
      setColor("transparent")
    }
    else {
      setColor(data.color)
    }
    setOpen(false)
  }, [data])

  return (
    <>
      <TableRow style={{ backgroundColor: color }}>
        <TableCell>
          <IconButton onClick={() => setOpen(!open)}>
            {open ?
              <ArrowDropUpIcon style={{ color: textColor }} /> :
              <ArrowDropDownIcon style={{ color: textColor }} />}
          </IconButton>
        </TableCell>
        <TableCell>
          <DateField label="Data" record={data} source="date" showTime
            style={{ color: data.color != null ? getContrastColor(data.color) : textColor, fontWeight: data.color !== null && getContrastColor(data.color) === 'white' || data.color === null && textColor === 'white' ? 'bold' : 'normal' }} />
        </TableCell>
        <TableCell>
          <TextField label="Urządzenie" record={data} source="deviceName"
            style={{ color: data.color != null ? getContrastColor(data.color) : textColor, fontWeight: data.color !== null && getContrastColor(data.color) === 'white' || data.color === null && textColor === 'white' ? 'bold' : 'normal' }} />
        </TableCell>
        <TableCell>
          <TextField label="Bilet" record={data} source="ticketCode"
            style={{ color: data.color != null ? getContrastColor(data.color) : textColor, fontWeight: data.color !== null && getContrastColor(data.color) === 'white' || data.color === null && textColor === 'white' ? 'bold' : 'normal' }} />
        </TableCell>
      </TableRow>
      <TableRow>
        <NoPaddingCell colSpan={4}>
          <Collapse in={open}>
            <Grid container style={{ marginTop: "1%", marginBottom: "1%" }} item>
              <CenterGrid item xs={5}>Rezerwacja:</CenterGrid>
              <CenterGrid item xs={7}>
                {data.bookingId == null ?
                  <div /> :
                  <BookingButton onClick={() => history.push({ pathname: `/${ResourceName.TICKET_DETAILS}/${data.bookingId}/show` })}>
                    {data.bookingId}
                  </BookingButton>}
              </CenterGrid>
              <hr style={{ width: "90%", marginTop: "1%", marginBottom: "1%" }} />
              <CenterGrid item xs={5}>Opis:</CenterGrid>
              <CenterGrid item xs={7}>{data.text}</CenterGrid>
            </Grid>
          </Collapse>
        </NoPaddingCell>
      </TableRow>
    </>
  )
}

const LogGrid = () => {
  const { ids, data } = useListContext()

  return (
    <TableContainer component={Paper}>
      <Table aria-label="a dense table" size="small" padding="none" >
        <TableHead>
          <TableRow>
            <TableCell style={{ width: "10%" }} />
            <TableCell style={{ width: "20%" }}>Data</TableCell>
            <TableCell style={{ width: "35%" }}>Urządzenie</TableCell>
            <TableCell style={{ width: "35%" }}>Bilet</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map(id =>
            <DashRow key={id} data={data[id] as ExtendedLog} />
          )}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export function LastLogList(props: ListProps) {

  return (
    <>
      <List
        {...props}
        perPage={9}
        exporter={false}
        pagination={false}
        actions={false}
        sort={{ field: 'date', order: 'DESC' }}>
        <LogGrid />
      </List>
    </>
  )
}
