import * as React from 'react'
import {
  Filter,
  FilterProps,
  List,
  ListProps,
  TextInput,
  useListContext
} from 'react-admin'
import { Table, TableHead, TableBody, TableRow, TableCell, TableContainer } from '@material-ui/core'
import { useHistory } from 'react-router'
import { ResourceName } from '../../../core/ResourceName'
import { EventOverallView } from '../../../core/stats/overall-view/event-overall-view'

const ListFilters = (props: Omit<FilterProps, 'children'>) => (
  <Filter {...props}>
    <TextInput label="Nazwa" source="name" alwaysOn />
  </Filter>
)

const OverallViewRow = ({ record }: { record: EventOverallView }) => {
  const { id, name, dateStart, dateEnd } = record
  const history = useHistory()
  const redirect = () => history.push(`/${ResourceName.STATS_OVERALL_VIEW}/${id}/show`)

  return (
    <TableRow hover style={{cursor: "pointer"}}>
      <TableCell onClick={redirect}>{id}</TableCell>
      <TableCell onClick={redirect}>{name}</TableCell>
      <TableCell onClick={redirect}>{dateStart.replace("T", " ")}</TableCell>
      <TableCell onClick={redirect}>{dateEnd.replace("T", " ")}</TableCell>
    </TableRow>
  )
}

const OverallViewGrid = () => {
  const { ids, data } = useListContext()
  return (
    <TableContainer>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Id</TableCell>
            <TableCell>Nazwa</TableCell>
            <TableCell>Data rozpoczęcia</TableCell>
            <TableCell>Data Zakończenia</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map((id) => <OverallViewRow key={id} record={data[id] as EventOverallView} />)}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export const OverallViewList = (props: ListProps) => (
  <List {...props} actions={false} pagination={false} filters={<ListFilters />}>
    <OverallViewGrid />
  </List>
)
