import React, { useEffect, useState } from 'react'
import {
  Card, CardContent, CardHeader, createStyles, Grid, makeStyles, Theme, Button, Paper, Switch,
  FormControlLabel, Dialog, DialogTitle, DialogContent, DialogActions, Badge, IconButton, Zoom, withStyles
} from '@material-ui/core'
import { deepPurple } from '@material-ui/core/colors'
import { TransitionProps } from '@material-ui/core/transitions'
import SearchIcon from '@material-ui/icons/Search';
import ListIcon from '@material-ui/icons/List';
import SettingsIcon from '@material-ui/icons/Settings';
import { ResourceContextProvider, useGetIdentity } from 'react-admin'
import { ResourceName } from '../core/ResourceName'
import { FindUserByTicket } from './common/FindUserByTicket'
import { LastLogList } from './logs/LastLogList'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    header: {
      textAlign: 'center',
      letterSpacing: theme.spacing(0.2),
    },
    subHead: {
      textAlign: 'center'
    },
    noBorderRow: {
      border: "none",
    },
    tableRow: {
      border: "black"
    }
  }),
)

const OpenDashboardSettingsModalButton = withStyles({
  root: {
    height: 44,
    width: 45,
    border: "solid thin",
    borderColor: deepPurple[300],
    boxShadow: "0px 0px 3px #9575cd"
  }
})(IconButton)

const Transition = React.forwardRef((
  props: TransitionProps,
  ref: React.Ref<unknown>,
) => <Zoom ref={ref} {...props} />);

const TicketFinder = () => (
  <Card style={{ width: "100%", marginBottom: "5px" }}>
    <CardHeader
      title="Znajdź użytkownika po kodzie biletu" />
    <CardContent>
      <FindUserByTicket />
    </CardContent>
  </Card>
)

const LastLogs = () => (
  <Card style={{ width: "100%", marginBottom: "5px" }}>
    <CardHeader
      title="Ostatnie logi" />
    <CardContent>
      <ResourceContextProvider value={ResourceName.LAST_LOGS}>
        <LastLogList basePath="/" title=" " />
      </ResourceContextProvider>
    </CardContent>
  </Card>
)

const ComponentsModal = ({ components, open, setOpen }:
  {
    components: { ticketfinder: boolean, dashlogs: boolean },
    open: boolean,
    setOpen: (change: boolean) => void
  }) => {
  const { identity } = useGetIdentity()

  const [componentsOpen, setComponentsOpen] = useState({
    ticketfinder: components.ticketfinder,
    dashlogs: components.dashlogs
  })

  const componentsChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setComponentsOpen({ ...componentsOpen, [event.target.name]: event.target.checked })
  }

  const handleAccept = () => {
    Object.entries(componentsOpen).map((component) => {
      localStorage.setItem(`${identity?.id}.${component[0]}`, component[1] ? '1' : '0')
      return component
    })
    setOpen(false)
  }

  useEffect(() => {
    setComponentsOpen({
      ticketfinder: localStorage.getItem(`${identity?.id}.ticketfinder`) === '1',
      dashlogs: localStorage.getItem(`${identity?.id}.dashlogs`) === '1'
    })
  }, [open, identity])

  return (
    <Dialog open={open} style={{ display: "flex", alignItems: "center", justifyContent: "center" }} onClose={() => setOpen(false)} TransitionComponent={Transition}>
      <Paper style={{ display: "flex", flexDirection: "column", width: "40vh" }}>
        <DialogTitle style={{ textAlign: "center" }}> Dashboard </DialogTitle>
        <DialogContent>
          <Grid container spacing={1}>
            <Grid item xs={6}>
              <Paper style={{
                width: "100%", height: "150px", display: "flex",
                alignItems: "center", justifyContent: "center", flexDirection: "column"
              }}>
                <SearchIcon />
                <FormControlLabel
                  control={
                    <Switch
                      checked={componentsOpen.ticketfinder}
                      name='ticketfinder'
                      onChange={componentsChange} />}
                  label="Wyszukiwarka biletów"
                  labelPlacement="top"
                  style={{ margin: "5%", textAlign: "center", width: "90%" }} />
              </Paper>
            </Grid>
            <Grid item xs={6}>
              <Paper style={{
                width: "100%", height: "150px", display: "flex",
                alignItems: "center", justifyContent: "center", flexDirection: "column"
              }}>
                <ListIcon />
                <FormControlLabel
                  control={
                    <Switch
                      checked={componentsOpen.dashlogs}
                      name='dashlogs'
                      onChange={componentsChange} />}
                  label="Ostatnie logi"
                  labelPlacement="top"
                  style={{ margin: "5%", textAlign: "center", width: "90%" }} />
              </Paper>
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button variant="outlined" onClick={handleAccept}>zapisz</Button>
          <Button variant="outlined" onClick={() => setOpen(false)}>anuluj</Button>
        </DialogActions>
      </Paper>
    </Dialog>
  )
}

const DashboardSettingsModalButton = ({setModalOpen}:{setModalOpen: (state: boolean) => void}) => {
  const { identity } = useGetIdentity()
  
  return (
    <div style={{marginTop: "5px"}}>
      {localStorage.getItem(`${identity?.id}.newfeaturedash`)
        ? <OpenDashboardSettingsModalButton onClick={() => { setModalOpen(true) }}>
          <SettingsIcon color="primary" />
        </OpenDashboardSettingsModalButton>
        : <Badge color="primary" variant="dot" overlap="circle">
          <OpenDashboardSettingsModalButton onClick={() => { localStorage.setItem(`${identity?.id}.newfeaturedash`, '1'); setModalOpen(true) }}>
            <SettingsIcon color="primary" />
          </OpenDashboardSettingsModalButton>
        </Badge>}
    </div>
  )
}

export const Dashboard = () => {
  const { identity } = useGetIdentity()
  const classes = useStyles()
  const [dashboardSettingsModalOpen, setDashboardSettingsModalOpen] = useState(false)

  useEffect(() => {
    ['dashlogs', 'ticketfinder',].map((component) => {
      if (!localStorage.getItem(`${identity?.id}.${component}`))
        localStorage.setItem(`${identity?.id}.${component}`, '1')
      return component
    })
  }, [identity])

  const handleModalOpen = (value: boolean) => {
    setDashboardSettingsModalOpen(value)
  }

  return (
    <Grid container spacing={1}>
      <ComponentsModal open={dashboardSettingsModalOpen} setOpen={setDashboardSettingsModalOpen} components={{
        ticketfinder: localStorage.getItem(`${identity?.id}.ticketfinder`) === '1',
        dashlogs: localStorage.getItem(`${identity?.id}.dashlogs`) === '1'
      }} />
      <Grid item xs={12} >
        <Card>
          <CardHeader
            className={classes.header}
            title="Witaj w aplikacji administracyjnej Sport Data"
            titleTypographyProps={{ variant: 'h4' }}
          />
        </Card>
      </Grid>
      <Grid item sm={6} xs={12}>
        {localStorage.getItem(`${identity?.id}.ticketfinder`) === '1' && <TicketFinder />}
      </Grid>
      <Grid item sm={6} xs={12} style={{ display: "flex", flexDirection: "column", alignItems: "flex-end", width: "10%" }}>
        {localStorage.getItem(`${identity?.id}.dashlogs`) === '1' && (<LastLogs />)}
        <DashboardSettingsModalButton setModalOpen={handleModalOpen} />
      </Grid>
    </Grid>
  )
}
