import React, { ChangeEvent, FC, useState } from 'react'
import {
    Box,
    Button,
    FormControl,
    withStyles,
    Input,
    FormHelperText,
    BoxProps
} from '@material-ui/core'
import SearchIcon from '@material-ui/icons/Search'

const RoundInputBox = withStyles({
    root: {
        borderRadius: 16,
        width: "90%",
        marginLeft: "5%",
        display: "flex",
        flexDirection: "row-reverse",
        border: "solid",
        borderColor: "#673ab7"
    }
})(Box)

const RoundInputBoxBtn = withStyles({
    root: {
        background: "#673ab7",
        variant: "contained",
        borderRadius: 11,
        borderTopLeftRadius: 0,
        borderBottomLeftRadius: 0,
        margin: 0,
        fontWeight: "bold",
        '&:hover': {
            background: "#7e57c2"
        }
    },
})(Button)

const RoundInputBoxFC = withStyles({
    root: {
        width: "100%",
        marginLeft: 13,
        marginTop: 2
    }
})(FormControl)

export type SearchInputProps = {
    label: string
    onValue: (value: string) => void
    disabled: boolean
    error: string,
    boxStyle: BoxProps | undefined
}

const SearchInput: FC<Readonly<SearchInputProps>> = ({
    label,
    onValue,
    disabled,
    error,
    boxStyle
}) => {
    const [inputError, setInputError] = useState(false)
    const [inputValue, setInputValue] = useState('')

    const handleInput = (event: ChangeEvent<HTMLInputElement>) => {
        setInputValue(event.target.value)
    }

    const handleClick = () => {
        if (inputValue) {
            setInputError(false)
            onValue(inputValue)
        } else {
            setInputError(true)
        }
    }

    return (
        <RoundInputBox style={boxStyle}>
            <RoundInputBoxBtn onClick={handleClick} disabled={disabled}>
                <SearchIcon style={{ color: "white" }} />
            </RoundInputBoxBtn>
            <RoundInputBoxFC disabled={disabled}>
                <Input disableUnderline onInput={handleInput} placeholder={label}
                    onKeyDown={(ev) => ev.key === 'Enter' && handleClick()} error={inputError || !!error} />
                {inputError && (
                    <FormHelperText error>Pole jest wymagane</FormHelperText>
                )}
                {!!error && <FormHelperText error>{error}</FormHelperText>}
            </RoundInputBoxFC>
        </RoundInputBox>
    )
}

export default SearchInput