import { Action } from "redux"
import { CHANGE_THEME } from "../constants/reducer.actions"

const identity = localStorage.getItem('username')

export type reducerType = {
    theme: {
        darkMode: 'light' | 'dark'
    }
}

const initialState = {
    darkMode: localStorage.getItem(`${identity}.darkMode`) === 'dark' ? 'dark' : 'light'
}

function themeReducer(state = initialState, action: Action<any>) {
    if (action.type === CHANGE_THEME) {
        if (state.darkMode === 'dark') localStorage.setItem(`${identity}.darkMode`, 'light')
        else localStorage.setItem(`${identity}.darkMode`, 'dark')
        return {
            ...state,
            darkMode: state.darkMode === 'dark' ? 'light' : 'dark'
        }
    }
    return state
}

export default themeReducer